@font-face {
    font-family: 'Bahiana';
    src: url('img/Bahiana-Regular.woff2') format('woff2'),
        url('img/Bahiana-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
  font-family: Bahiana;
}

.howto {
  margin-top:50px;
}

.next {
  position: absolute;
  right: 80px;
  top:80px;
}

.next_bt {
  background-image: url( "img/next.png");
}

.prev {
  position: absolute;
  left: 80px;
  top: 80px;
}

input:hover {
  opacity: .5;
}

.takim_adi {
  position: relative;
  font-size: 67px;
  color: #762f10;
  margin-top:120px;
}

.takim_input {
  position: relative;
  top: 50px;
}

.takim_i {
  border: 0px solid #444444;
  font-family: Bahiana;
  height:94px;
  width:361px;
  background:url(img/input.png) no-repeat right top;
  color:#fff;
  padding-left:4px;
  font-size:48px;
  text-align: center;
}

.takim_i::placeholder {
    color:#fff;
    opacity:.5;
}

.cat_c { margin-left:120px; margin-top:40px; padding-top: 40px;}

.cat_t {
    color:#FFF;
    font-size: 28px;
}

a {
  color: #fff;
  text-decoration: none;
  font-size: 28px;
 }

.cat {
  background-image: url("img/cat.png");
  width: 372px;
  height: 50px;
  float: left;
  margin: 10px;
  padding-top: 12px;
}

.cat-completed {
    opacity: .5;
}

.cat_a {
  position: absolute;
  left: 170px;
  top: 320px;
  background-image: url("img/cat_a.png");
  width: 665px;
  height: 104px;
  color: #fff;
  padding-top: 24px;
  font-size: 57px;
}

.oynayan {
  position: absolute;
  left: 80px;
  bottom: 80px;
  font-size:28px;
    color: #762f10;
}

.word {
  position: relative;
  background-image: url("img/word.png");
  width: 757px;
  height: 166px;
  top:160px;
  left:140px;
  font-size: 74px;
  color: #762f10;

}

.word_w {
  position: relative;
  top: 36px;
}

.word_t {
  position: relative;
  top: 190px;
}

.butons {
  position: relative;
  top: 210px;
}

.countdown {
  position: absolute;
  background-image: url(img/countdown.png);
  width:131px;
  height: 105px;
  right: 80px;
  top: 80px;
  color: #fff;
  font-size:67px;
  padding-top: 26px;
}

.g_bt {
  margin-right: 20px;
}

.timeout > .word, .timeout > .word_t, .timeout > .butons > .g_bt_pas {
    opacity: 0.5;
}

.r_sheet {
  width: 544px;
  height: 601px;
  left: 160px;
  top: 90px;
  position: absolute;
  background-image: url(img/result_sheet.png);
}

.r_final {
  width: 680px;
  height: 479px;
  left: 120px;
  top: 150px;
  position: absolute;
  background-image: url(img/result_final.png);
}

.tablo {
  position: absolute;
  top: 100px;
  left: 0;
  font-size: 74px;
  color: #762f10;
}

.tablo .takim-adi {
    font-size:40px;
    width:180px;
    padding-right:20px;
    height:115px;
    color:#FFF;
}

.tablo .soru-sayisi, .tablo .dogru-sayisi {
    width:150px;
}

.tablo_f {
  position: absolute;
  top: 160px;
  left: 0px;
  font-size: 74px;
  color: #762f10;
}

.tablo_f .takim-adi {
    height:130px;
    width:250px;
    padding-right:45px;
    font-size:50px;
    color:#FFF;
}

.tablo_f .soru-sayisi {
    width:162px;
}

.tablo_f .dogru-sayisi {
    width:162px;
}

.winner {
  position: absolute;
  background: url(img/result-cover.png) no-repeat center top;
  width: 520px;
  height: 225px;
  top: 110px;
  left: 260px;
  padding-top:160px;
  color: #bd6517;
  font-size: 74px;
  text-align: center;
}

.winner-text-1 {
    position:absolute;
    bottom: 130px;
    left:250px;
}

.winner-text-2 {
    position:absolute;
    bottom: 130px;
    left:165px;
}
